.container-home {

    .banner {
        display: flex;
        position: relative;
        width: 1110px;
        height: 640px;
        border-radius: 15px;
        background-color: $orange-light;
        padding: 0 95px;
        overflow: hidden;
        margin: 0 auto 160px;

        section {
            position: relative;
            align-self: center;
            width: 500px;
            height: auto;
            color: $color-white;
            z-index: 1;

            h1 {
                margin-bottom: 11px;
            }

            p {
                display: block;
                margin-bottom: 40px;
                width: 445px;
            }
            
            li {
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $color-white;
                color: $color-grey;
                width: 152px;
                height: 56px;
                border-radius: 15px;

                &:hover {
                    background-color: $fade-orange;
                    color: $color-white;
                }
            }
        }

        img {
            position: absolute;
        }

        .background-phone {
            right: -75px;
            top: -10px;
        }

        .background-circle {
            right: 0;
            top: 0;
        }
    }

    .interaction {
        display: flex;
        flex-flow: column wrap;
        gap: 24px;
        width: 1110px;
        height: 640px;
        margin: 0 auto 160px;

        .project {
            border-radius: 15px;
            overflow: hidden;
            position: relative;

            &:hover span {
                display: block;
            }

            span {
                display: none;
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: $fade-orange;
                opacity: 0.7;
                cursor: pointer;
                z-index: 10;
            }
        }

        .project:first-child {
            width: 541px;
            height: 640px;
            background-image: url(../../../public/assets/home/desktop/image-web-design-large.jpg);
        }

        .project:nth-child(2) {
            width: 541px;
            height: 308px;
            background-image: url(../../../public/assets/home/desktop/image-app-design.jpg);
        }

        .project:last-child {
            width: 541px;
            height: 308px;
            background-image: url(../../../public/assets/home/desktop/image-graphic-design.jpg);
        }

        section {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: max-content;
            height: 94px;
            text-align: center;
            color: $color-white;

            h2 {
                margin-bottom: 24px;
            }

            p {
                display: inline-flex;
                align-items: center;
                cursor: pointer;

                img {
                    width: 8px;
                    height: 15px;
                    margin-left: 21px;
                }
            }
        }
    }

    .quality {
        display: flex;
        justify-content: space-between;
        width: 1110px;
        height: 412px;
        margin: 0 auto 380px;

        div {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 350px;
            height: 412px;

            img {
                width: 202px;
                border-radius: 100%;
                margin-bottom: 48px;
            }

            section {
                text-align: center;

                h3 {
                    margin-bottom: 32px;
                    letter-spacing: 5px;
                }
            }
        }
    }

    //modification dynamique
    .change {
        height: 252px;

        img {
            display: none;
        }

        section {
            text-align: center;
            margin: auto;
            width: 420px;
            height: 124px;

            button {
                display: none;
            }
        }
    }

    .hide {
        display: none;
    }

    .resize {
        height: 308px;
        margin-bottom: 380px;
    }

    .presentation {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 30px;
        margin: 0 auto 160px;
        width: 1112px;
        height: auto;
    }

    .none {
        display: none;
    }

    .block {
        display: block;
    }
}


@media screen and (max-width: 1200px) {
    .container-home {
        .banner {
            width: 95%;
            height: 640px;
            border-radius: 15px;
            padding: 0 95px;
    
            section {
                height: auto;
    
                h1 {
                    margin-bottom: 11px;
                }
    
                p {
                    margin-bottom: 40px;
                }
            }
        }

        .interaction {
            flex-flow: column;
            align-items: center;
            width: 80%;
            height: auto;
            margin: 0 auto 120px;
            .project:first-child, .project:nth-child(2), .project:last-child {
                width: 100%;
                height: 200px;
                background-position: center;
                background-size: cover;
            }

            .project:first-child {
                background-image: url(../../../public/assets/home/tablet/image-web-design.jpg);
            }
    
            .project:nth-child(2) {
                background-image: url(../../../public/assets/home/tablet/image-app-design.jpg);
            }
    
            .project:last-child {
                background-image: url(../../../public/assets/home/tablet/image-graphic-design.jpg);
            }
        }

        .quality {
            flex-direction: column;
            width: 95%;
            height: 670px;
            margin: 0 auto 380px;

            div {
                flex-direction: row;
                width: 100%;
                height: auto;

                img {
                    margin: 0 48px 0 0;
                }
    
                section {
                    text-align: left;
    
                    h3 {
                        margin-bottom: 16px;
                    }
                }
            }
        }

        .presentation {
            width: 95%;
        }

        .change {
            height: 252px;
        }

        .resize {
            margin-bottom: 380px;
        }
    }
}


@media screen and (max-width: 1050px) {
    .container-home {
        .banner {
            padding: 60px 0 0;
            flex-direction: column;
            height: 843px;
            margin-bottom: 120px;

            section {
                align-self: inherit;
                width: 573px;
                margin: 0 auto;
                text-align: center;
    
                h1 {
                    margin-bottom: 11px;
                }
    
                p {
                    margin: 0 auto 19px;
                }
                
                li {
                    margin: auto;
                    z-index: 120;
                }
            }

            .background-phone {
                right: 50%;
                transform: translateX(50%);
                top: 280px;
            }
    
            .background-circle {
                right: -70px;
                top: 100px;
                z-index: 0;
            }
        }
        .change {
            height: 252px;
        }
    }
}

@media screen and (max-width: 620px) {
    .container-home {
        .banner {
            padding-top: 80px;
            width: 100%;
            border-radius: 0;
            
            section {
                width: 95%;

                h1, p {
                    width: 100%;
                }
            }
        }

        .interaction {
            width: 95%;

            .project:first-child, .project:nth-child(2), .project:last-child {
                height: 250px;
            }

            .project:first-child {
                background-image: url(../../../public/assets/home/mobile/image-web-design.jpg);
            }
    
            .project:nth-child(2) {
                background-image: url(../../../public/assets/home/mobile/image-app-design.jpg);
            }
    
            .project:last-child {
                background-image: url(../../../public/assets/home/mobile/image-graphic-design.jpg);
            }
        }

        .quality {
            height: auto;
            margin: 0 auto 311px;

            div:not(:last-child) {
                margin-bottom: 80px;
            }

            div {
                flex-direction: column;
                align-items: center;

                img {
                    margin: 0 0 30px;
                }
    
                section {
                    text-align: center;
    
                    h3 {
                        margin-bottom: 16px;
                    }
                    p {
                        text-align: center ;
                    }
                }
            }
        }

        .change {
            height: 320px;
            section {
                h1 {
                    margin-bottom: 30px;
                }
            }
        }
    }
}

@media screen and (max-width: 330px) {
    .container-home {
        .banner {
            .background-phone {
                top: 320px;
            }
        }
    }    
}