.div-talk {
    display: flex;
    align-items: center;
    position: absolute;
    background-image: url(../../../public/assets/shared/desktop/bg-pattern-small-circle.svg);
    background-size: 292px 292px;
    background-position: right 0 top -146px;
    background-color: $orange-light;
    left: 50%;
    bottom: 321px;
    transform: translateX(-50%);
    width: 1111px;
    height: 292px;
    border-radius: 15px;
    padding: 0 95px;

    section {

        margin-right: auto;
        width: 459px;
        height: auto;

        h2 {
            width: 280px;
            color: $color-white;
            margin-bottom: 15px;
        }       

        p {
            color: $color-white;
        }
    }

    li {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $color-white;
        color: $color-grey;
        width: 152px;
        height: 56px;
        border-radius: 15px;

        &:hover {
            background-color: $fade-orange;
            color: $color-white;
        }
    }
}

@media screen and (max-width: 1200px) {
    .div-talk {
        bottom: 321px;
        width: 95%;
        padding: 0 95px;
    }  
}

@media screen and (max-width: 900px) {
    .div-talk {
        padding: 0 40px;
    }  
}

@media screen and (max-width: 755px) {
    .div-talk {
        display: flex;
        flex-direction: column;        
        bottom: 626px;
        height: auto;
        padding: 57px 0;

        section {
            margin: 0 auto 32px;
            width: 80%;
            text-align: center;

            h2 {
                width: 335px;
                margin: 0 auto 15px;
            }       

            p {
                color: $color-white;
            }
        }
    }
}


@media screen and (max-width: 470px) {
    .div-talk {
        section {
            h2 {
                width: 100%;
            }
        }
    }
}