.container-company {

    .banner {
        position: relative;
        display: flex;
        margin: 0 auto 160px;
        width: 1110px;
        height: 480px;
        border-radius: 15px;
        overflow: hidden;
        background-color: $orange-light;

        .circle {
            position: absolute;
            bottom: 0;
        }

        div {
            z-index: 1;
            width: 476px;
            height: 480px;
            background-image: url(../../../public/assets/about/desktop/image-about-hero.jpg);
            background-position: 50% 50%;
            background-size: cover;
        }

        section {
            margin: 0 85px 0 auto;
            width: 458px;
            height: 210px;
            align-self: center;
            color: $color-white;

            h1 {
                margin-bottom: 32px;
            }
        }
    }

    .div-presentation {
        display: flex;
        width: 1110px;
        height: 640px;
        background-color: $color-beige;
        border-radius: 15px;
        overflow: hidden;
        margin: 0 auto 160px;

        .background-image {
            width: 476px;
            height: 100%;
            background-size: cover;
            background-position: center;
        }

        .background-image:first-child {
            background-image: url(../../../public/assets/about/desktop/image-world-class-talent.jpg);
        }

        .background-image:last-child {
            background-image: url(../../../public/assets/about/desktop/image-real-deal.jpg);
        }

        section {
            align-self: center;
            text-align: justify;
            width: 445px;
            height: auto;
            margin: 0 96px;

            h2 {
                margin-bottom: 24px;
                color: $orange-light;
            }

            p {
                color: $color-grey;
            }
        }
    }

    .bottom {
        margin-bottom: 384px;
    }
}

@media screen and (max-width: 1200px) {
    .container-company {
        .banner {
            width: 95%;
    
            .circle {
                position: absolute;
                bottom: 0;
            }
    
            section {
                margin: auto;
            }
        }

        .div-presentation {
            width: 95%;

            section {
                margin: auto;
            }
        }
    }
}

@media screen and (max-width: 1040px) {
    .container-company {
        .banner {
            flex-direction: column-reverse;
            width: 95%;
            height: 632px;

            div {
                width: 100%;
                height: 320px;
                background-image: url(../../../public/assets/about/tablet/image-about-hero.jpg);
            }
    
            .circle {
                left: -12px;
                bottom: 120px;
            }
    
            section {
                text-align: center;
                width: 573px;
                height: auto;
                margin: auto;
            }
        }

        .div-presentation:last-of-type {
            flex-direction: column-reverse;
        }

        .div-presentation {
            flex-direction: column;
            height: 736px;
            margin: 0 auto 120px;

            .background-image {
                width: 100%;
                height: 320px;
            }

            .background-image:first-child {
                background-image: url(../../../public/assets/about/tablet/image-world-class-talent.jpg);
            }
    
            .background-image:last-child {
                background-image: url(../../../public/assets/about/tablet/image-real-deal.jpg);
            }
    
            section {
                margin: auto;
                text-align: center;
                width: 580px;
                height: auto;
            }
        }

        .bottom {
            flex-direction: column-reverse;
            margin-bottom: 310px;
        }
    }
}


@media screen and (max-width: 640px) {
    .container-company {
        .banner {
            width: 100%;
            border-radius: 0;
            height: auto;
            margin: 0 auto;
    
            section {
                margin: 80px auto;
                height: auto;
                width: 95%;
            }
        }

        .div-presentation {
            width: 100%;
            border-radius: 0;
            height: auto;

            section {
                margin: 80px auto;
                width: 95%;
            }
        }
    }
}