header {
    display: flex;
    justify-content: space-between;
    margin: 64px auto 67px;
    width: 1111px;
    height: 24px;
    z-index: 20;

    ul {
        display: flex;
        align-items: center;
        gap: 42px;
        height: 24px;

        a {
            color: $color-grey;

            &:hover {
                text-decoration: underline;
            }
        }
    }

    .logo {
        width: 196px;
        z-index: 20;
    }

    .toggle {
        display: none;
        cursor: pointer;        
    }
}

@media screen and (max-width: 1200px) {
    header {
        width: 95%;
    }
}

@media screen and (max-width: 900px) {
    header {
        margin: 35px auto;
    }
}

@media screen and (max-width: 620px) {

    header {
        width: 95%;

        .backdrop {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 60px;
            background: rgba($color: #000000, $alpha: 0.8);
            z-index: 20;
        }

        .toggle {
            display: block;
        }

        .open-menu {
            transform: scaleY(1);
        }

        ul {
            align-items: inherit;
            flex-direction: column;
            position: absolute;
            top: 59px;
            left: 0;
            z-index: 22;
            padding: 48px 24px;
            gap: 32px;          
            width: 100%;
            height: 235px;
            background: #000;
            transform: scaleY(0);
            transform-origin: top;
            transition: transform 0.4s;
    
            a {
                color: $color-white;
                font-size: 24px;
                line-height: 25px;
    
                &:hover {
                    text-decoration: none;
                }
            }
        }
    }
}