#root {
    position: relative;
    width: 1440px;
    margin: auto;
}

html {
    scroll-behavior: smooth;
}

h1, h2, h3, h4, p, button, a {
    font-family: 'Jost', sans-serif;
    font-style: normal;
}

h1 {
    font-weight: 500;
    font-size: 48px;
    line-height: 48px;
}

h2 {    
    font-weight: 500;
    font-size: 40px;
    line-height: 48px;
}

h3 {
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
}

h4 {
    font-weight: 500;
    font-size: 15px;
    line-height: 22px;
}

p {
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
}

a {
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    letter-spacing: 2px;
}

$color-beige : #FDF3F0;
$color-white : #FFF;
$color-black : #1D1C1E;
$color-grey: #333136;

$orange-light: #E7816B;
$fade-orange : #FFAD9B;

@media screen and (max-width: 1457px) {
    #root {
        width: 100%;
    }
}

@media screen and (max-width: 488px) {
    h1 {        
        font-weight: 500;
        font-size: 36px;
        line-height: 36px;
    }
   
}