footer {
    width: 100%;
    background-color: $color-black;

    .container-footer {
        width: 1111px;
        margin: auto;
    }

    .large {
        height: 393px;
        padding: 142px 0;
    }

    .small {
        height: 321px;
        padding: 72px 0;
    }

    .navigation {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;

        .logo {
            width: 202px;
        }
        
        ul {
            display: flex;
            gap: 42px;

            a {
                color: $color-white;

                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
    
    .div-information {
        border-top: 1px solid rgba($color: #FFF, $alpha: 0.1);
        display: flex;
        width: 1111px;
        height: 109px;
        padding-top: 31px;
        color: $color-white;

        .information:first-child {
            margin-right: 205px;
        }

        .information:nth-child(2) {
            margin-right: auto;
        }
        
        .div-reseaux {
            display: flex;
            align-self: flex-end;
            width: 184px;
            height: 24px;
            gap: 16px;

            img {
                cursor: pointer;
            }
        }
    }
}

@media screen and (max-width: 1200px) {
    footer {    
        .container-footer {
            width: 95%;
        }
        
        .div-information {
            width: 100%;
            justify-content: space-between;

            .information:first-child, .information:nth-child(2) {
                margin-right: 0;
            }
        }
    }
}

@media screen and (max-width: 755px) {
    footer {
        .large {
            height: 815px;
        }

        .small {
            height: 626px;
        }        
        
        .large {
            padding: 253px 0;
        }
       
    
        .navigation {
            flex-direction: column;
            justify-content: inherit;
            height: 205px;
    
            .logo {
                margin-bottom: 32px;
            }
            
            ul {
                padding-top: 33px;
                text-align: center;
                flex-direction: column;
                gap: 32px;
                width: 100%;
                border-top: 1px solid $color-white;

                a {
                    font-size: 14px;
                    line-height: 14px;
                }
            }
        }
        
        .div-information {
            padding-top: 0;
            border-top: none;
            flex-direction: column;
            align-items: center;
            height: 260px;            
            text-align: center;
            
            .div-reseaux {
                align-self: center;
            }
        }
    }
}